<script>
import { ref } from "@vue/reactivity";
import AppButton from "../components/Button";
import AppInput from "../components/Input";
import AlertDialog from "../components/AlertDialog";
export default {
  components: { AppButton, AppInput, AlertDialog },
  setup() {
    const username = ref("");
